<template>
    <div class="content-bg">
      <div class="top-title">协议流量
          <v-flowchartfrom class="from-wapper" refreshBus="protocolFlowRefreshBus" dataBus="protocolFlowDataBus"></v-flowchartfrom>
      </div>
      <div class="item-wapper">
        <div class="item-title">传输层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.TUData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">传输层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.TUData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">应用层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.appData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">应用层协议长度统计</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.appData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">协议长度综合统计</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.agreementData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">协议长度综合统计</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.agreementData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vFlowchartfrom from '../common/flowChartsFrom'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "protocolFlow",
        data() {
            return {
                interTime:'',
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:3600,
                //传输层协议长度统计
                TUData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'传输层\n协议',
                        yAxisName:'数据包长度/KB',
                        rotate:'20',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                TUData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //应用层协议长度统计
                appData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'应用层\n协议',
                        yAxisName:'数据包长度/KB',
                        rotate:'20',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                appData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                //协议长度综合统计
                agreementData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'协议',
                        yAxisName:'数据包长度/KB',
                        rotate:'20',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                agreementData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('protocolFlowRefreshBus',(val)=>{
                    this.refreshIntTime = val;
                    clearInterval(this.interTime);
                    this.setInterFun()
            })
            /*监听数据时间改变*/
            bus.$on('protocolFlowDataBus',(val)=>{
                this.dataTime = val;
            })

        },
        mounted(){
            //第一次获取数据
            /*获取传输层协议长度统计*/
            this.getTUData(this.dataTime);
            /*获取应用层协议长度统计*/
            this.getAppData(this.dataTime);
            /*获取协议长度综合统计*/
            this.getAgreementData(this.dataTime);

        },
        methods:{
            /*获取传输层协议长度统计*/
            getTUData(dataTime){
                this.$nextTick(()=>{

                    this.$post('/flow/getTransportLength.do',{
                      timeInterval:dataTime
                    })
                        .then(res=>{
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                             this.TUData.xAxisArr = xns1;
                             this.TUData.yAxisArr = yvs1;
                             this.TUData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{

                        })
                })
            },
            /*获取应用层协议长度统计*/
            getAppData(dataTime){
                this.$nextTick(()=>{

                    this.$post('/flow/getApplicationLength.do',{
                      timeInterval:dataTime
                    })
                        .then(res=>{
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                             this.appData.xAxisArr = xns1;
                             this.appData.yAxisArr = yvs1;
                            this.appData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{

                        })
                })
            },
            /*获取协议长度综合统计*/
            getAgreementData(dataTime){
                this.$nextTick(()=>{
                    this.$post('/flow/getMultipleLength.do',{
                      timeInterval:dataTime
                    })
                        .then(res=>{
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res){
                                /*yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })*/
                                for(let j in res[i]){
                                    xns1.push(j);
                                    yvs1.push(res[i][j]);
                                    yvs2.push({
                                        name:j,
                                        value:res[i][j]
                                    })
                                }
                            }
                             this.agreementData.xAxisArr = xns1;
                             this.agreementData.yAxisArr = yvs1;
                            this.agreementData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{

                        })
                })
            },
            /*计时器*/
            setInterFun(){
              /*循环获取数据*/
              this.interTime = setInterval(()=>{
                /*获取传输层协议长度统计*/
                this.getTUData(this.dataTime);
                /*获取应用层协议长度统计*/
                this.getAppData(this.dataTime);
                /*获取协议长度综合统计*/
                this.getAgreementData(this.dataTime);
              },this.refreshIntTime);
            }
        },
        beforeRouteEnter(to, from, next){
          next(vm => {
            vm.setInterFun();
          });
        },
        beforeRouteLeave(to, from, next) {
          clearInterval(this.interTime);
          next();
        },
        components:{
            vFlowchartfrom,
            vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



